<!-- 订单商品 --> <!-- 订单编号 -->  <!-- 订单商品数据 -->
<template>
  <div class="order">
    <van-nav-bar title="我的订单" left-text="返回" left-arrow @click-left="goback" />
    <van-tabs
      v-model="activeName"
      @change="selectStatus"
      color="rgb(249,187,50)"
      line-height="2"
      title-active-color="rgb(249,187,50)"
    >
      <van-tab :title="item.title" :name="item.name" v-for="(item,index) in tabLists" :key="index">
        
        <div v-if="isHas">
          <van-empty
            class="custom-image"
            image="https://img.yzcdn.cn/vant/custom-empty-image.png"
            description="快去逛逛吧💗"
          />
        </div>

        <div v-else class="order-content">
          <div class="order-items">

            <div class="order-item" v-for="(item,index) in orderNos" :key="index">

              <div class="order-item-title">

                <div class="fl order-no">{{item}}</div>

                <div class="fr" v-if="orderAllData[item].status == 2">
                  <span class="confirm">已收货</span>
                  <span class="delete">
                    <van-icon class="delete-icon" name="delete" @click="deleteOrder(item)" />
                  </span>
                </div>

                <div class="fr" v-else>
                  <span class="confirm" @click="comfirmProducts(item)">确认收货</span>
                </div>
              </div>
              <!-- 订单商品 -->
              <div class="items" v-for="(v,i) in orderAllData[item].data" :key="i">
                <div class="clearfix">
                  <div class="fl pro-img">
                    <img class="auto-img" :src="v.smallImg" alt />
                  </div>
                  <div class="fl pro-info">
                    <div class="fl">
                      <div class="names">
                        <div class="zh-name">{{v.name}}</div>
                        <div class="en-name">{{v.enname}}</div>
                      </div>
                      <div class="rule">{{v.rule}}</div>
                    </div>
                    <div class="fr">
                      <div class="price">￥{{v.price}}</div>
                      <div class="count">x{{v.count}}</div>
                    </div>
                  </div>
                </div>
              </div>

              <div class="order-total clearfix">
                <div class="date fl">{{orderAllData[item].date | dateFormat}}</div>
                <div class="fr clearfix">
                  <span class="fl total-count">共计{{orderAllData[item].count}}件商品 合计：</span>
                  <span class="fl total-price">￥{{orderAllData[item].total}}</span>
                </div>
              </div>
            </div>

          </div>
        </div>
        
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import "../assets/css/order.less";
export default {
  data() {
    return {
      tabLists: [
        {
          title: "全部订单",
          name: "0"
        },
        {
          title: "待收货",
          name: "1"
        },
        {
          title: "已收货",
          name: "2"
        }
      ],
      activeName: "0",
      isRecieve: false,
      orderNos: [], //订单编号
      orderAllData: {}, //订单商品
      isHas:true
    };
  },

  methods: {
    goback(name) {
      this.$router.push({ name: "My" });
    },
    //切换状态
    selectStatus(a) {
      
      this.getOrder();
    },
    //获取订单商品 切换状态也需要使用
    getOrder() {
      let tokenString = localStorage.getItem("token");
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method: "GET",
        url: "/findOrder",
        params: {
          appkey: this.appkey,
          tokenString,
          status: this.activeName
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 70000) {
            //按照订单编号分类oids 先获取后分类 .过滤重复编号
            let oids = [];
            result.data.result.forEach(v => {
              if (oids.indexOf(v.oid) === -1) {
                oids.push(v.oid);
                
              }
            });
            this.orderNos = oids; //订单商品编号
            

            let orderNumber = {};
            result.data.result.forEach(v => {
              if (!orderNumber[v.oid]) {
                //编号不存在 就 添加新订单
                orderNumber[v.oid] = {
                  data: [v],
                  status: v.status, //状态
                  date: v.createdAt, //时间
                  count: v.count,
                  total: v.price * v.count
                };
              } else {
                orderNumber[v.oid].count += v.count;
                orderNumber[v.oid].total += v.count * v.price;
                orderNumber[v.oid].data.push(v);
              }
            });
            this.orderAllData = orderNumber;
            
            if(this.orderNos.length > 0){
              this.isHas = false;
            }else{
              this.isHas = true;
            }
            
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },
    //确认收货 切换为 已完成
    comfirmProducts(oid) {
      let tokenString = localStorage.getItem("token");
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method: "POST",
        url: "/receive",
        data: {
          appkey: this.appkey,
          tokenString,
          oid
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 80000) {
            //点击改变状态为2 ，如果待收货 就删除页面订单数据
            this.orderAllData[oid].status = 2;
            if (this.activeName == 1) {
              let index = this.orderNos.indexOf(oid);
              this.orderNos.splice(index, 1);
            }
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },
    //删除商品
    deleteOrder(oid) {
      let tokenString = localStorage.getItem("token");
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method: "POST",
        url: "/removeOrder",
        data: {
          appkey: this.appkey,
          tokenString,
          oid
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 90000) {
            let index = this.orderNos.indexOf(oid);
            this.orderNos.splice(index, 1);
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    }
  },
  created() {
    this.getOrder();
  }
};
</script>